<template>
  <div class="info">
    <h2 class="text-center"></h2>
    <form class="info_form d-flex">
      <div class="info_form_item m-1">
        <label for="name"></label>
        <input v-model="information.name" type="text" name="name" id="name" />
      </div>
      <div class="info_form_item m-1">
        <label for="tel"></label>
        <input v-model="information.phone" type="tel" name="phone" id="tel" />
      </div>
      <div class="info_form_item m-1">
        <label for="email"></label>
        <input
          v-model="information.email"
          type="email"
          name="email"
          id="email"
        />
      </div>
      <div class="info_form_item m-1">
        <label></label>
        <input v-model="information.date" type="date" name="date" />
      </div>
      <div class="info_form_item m-1">
        <label></label>
        <!---->
        <input v-model="information.time" type="time" />
      </div>
      <div class="info_form_item m-1 d-flex">
        <label></label>
        <div class="select-wrapper position-relative">
          <select v-model="information.seat" class="w-100 h-100" name="seat">
            <option value="" disabled selected>0</option>
            <option value="2">2</option>
            <option value="3">3</option>
            <option value="4">4</option>
          </select>
        </div>
      </div>
    </form>
    <div class="d-flex justify-content-around mt-1">
      <div class="align-self-center mr-2">
        <input
          v-model="isCurrentUser"
          @change="sameAsCurrentUser"
          type="checkbox"
          id="currentUser"
        />
        <label for="currentUser"></label>
      </div>
      <div class="button_wrapper mt-4">
        <button @click="toggleCart" class="button_wrapper--back mr-2 p-1">
          
        </button>
        <button
          @click="confirmToPay"
          type="submit"
          class="button_wrapper--pay p-1"
        >
          
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { Confirm, Toast } from '../../utils/helpers'
import { mapState } from 'vuex'
export default {
  data() {
    return {
      information: {
        name: '',
        phone: '',
        date: '',
        time: '',
        email: '',
        userId: '',
        seat: 0
      },
      isCurrentUser: false
    }
  },
  computed: {
    ...mapState(['currentUser'])
  },
  methods: {
    toggleCart() {
      this.$emit('after-toggle-cart')
    },
    confirmToPay() {
      //
      const { name, phone, date, time, seat, email } = this.information
      if (!name || !phone || !date || !time || !seat || !email) {
        Toast.fire({
          icon: 'error',
          title: ''
        })
        return
      }

      this.information['userId'] = this.currentUser.id

      Confirm.fire({
        title: '',
        text: '',
        confirmButtonText: '',
        cancelButtonText: '..'
      }).then(res => {
        if (res.isConfirmed) {
          this.$emit('after-confirm-pay', this.information)
        }
      })
    },
    sameAsCurrentUser() {
      if (this.isCurrentUser) {
        this.information = {
          ...this.information,
          name: this.currentUser.name,
          phone: this.currentUser.phone,
          email: this.currentUser.email
        }
      }
    }
  }
}
</script>

<style lang="scss">
@import '@/assets/css/components/ReservationPage/CartInfo';
</style>
